* {
  box-sizing: border-box;
}

.App {
  font-family: sans-serif;
  text-align: center;
  width: 400px;
  margin: 0 auto;
}

.wrapper-div video {
  border-color: tomato;
}

video {
  border: solid 10px transparent;
  margin: 15px 0;
  width: 400px;
  display: block;
}

button {
  margin: 0 10px 0 0;
  display: inline-block;
  color: #333;
  cursor: pointer;
  line-height: 1;
  font-weight: bold;
  border: 2px solid #333;
  background-color: #fff;
  padding: 8px 11px;
  min-width: 70px;
  text-decoration: none;
}
button:hover,
button:focus {
  border-color: #888;
  text-decoration: underline;
}

.invert {
  filter: invert(1);
}
